.container {
    margin: 1rem;
    padding: 1rem;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    background: #cccccc;
    -webkit-box-shadow: 0 8px 6px -6px black;
    -moz-box-shadow: 0 8px 6px -6px black;
    box-shadow: 0 8px 6px -8px black;
}

h1 {
    font-size: 3rem;
}

.Input {
    margin: 1.5rem auto;
    width: 30%;
    border-radius: 7px;
    border: 5px solid yellow;
}

.col {
    display: flex;
    flex-direction: column;
    background: #e6e6e6;
    width: 100%;
    padding: 1rem;
}

.inp {
    background: #e6e6e6;
    padding: 0.7rem 1rem;
    font-size: 1.5rem;
    width: 100%;
    border: none;
    outline: none;
}

.grid {
    width: 100%;
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
}

.card {
    background: #e6e6e6;
    margin: 1.5rem;
    padding: 1.5rem;
    border-radius: 8px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 50%);
}

.title {
    font-size: 1.5rem;
    padding-bottom: 1rem;
}

.details {
    font-size: 1.5rem;
}

footer {
    font-size: 1.5rem;
    text-align: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 1rem;
    background: #191919;
    color: white;
}